.button {
    display: inline-block; /* Делаем кнопку блочным элементом */
    padding: 10px 20px; /* Внутренний отступ кнопки */
    font-size: 16px; /* Размер шрифта */
    font-weight: bold; /* Жирный шрифт */
    text-align: center; /* Выравнивание текста по центру */
    text-decoration: none; /* Убираем подчеркивание у ссылки (если используется как ссылка) */
    cursor: pointer; /* Указатель при наведении */
    border: none; /* Убираем границу */
    border-radius: 4px; /* Радиус углов кнопки */
    transition: background-color 0.3s, color 0.3s; /* Плавный переход цветов */
}

.disabled {
    opacity: 0.5; /* Прозрачность для задизейбленной кнопки */
    cursor: not-allowed; /* Изменяем курсор для задизейбленной кнопки */
}

.secondary {
    background-color: #f0f0f0; /* Цвет фона вторичной кнопки */
    color: #333; /* Цвет текста вторичной кнопки */
}

.default {
    background-color: #06c; /* Цвет фона кнопки по умолчанию */
    color: #fff; /* Цвет текста кнопки по умолчанию */
}

.red {
    background-color: #ff0000; /* Цвет фона красной кнопки */
    color: #fff; /* Цвет текста красной кнопки */
}

.default:hover {
    background-color: #004080; /* Цвет фона при наведении на кнопку по умолчанию */
}

.red:hover {
    background-color: #cc0000; /* Цвет фона при наведении на красную кнопку */
}

.secondary:hover {
    background-color: #d9d9d9; /* Цвет фона при наведении на вторичную кнопку */
}

.default:active {
    background-color: #003366; /* Цвет фона при активации кнопки по умолчанию */
}

.red:active {
    background-color: #990000; /* Цвет фона при активации красной кнопки */
}

.secondary:active {
    background-color: #b3b3b3; /* Цвет фона при активации вторичной кнопки */
}

.button:disabled {
    /* Стили для задизейбленной кнопки */
    cursor: not-allowed;
    opacity: 0.5;
}
