.input:focus {
    border-color: #06c; /* Цвет рамки при фокусе */
    box-shadow: 0 0 5px rgba(0, 102, 204, 0.5); /* Тень при фокусе */
}

.input {
    width: 100%; /* Ширина инпута */
    padding: 8px; /* Внутренний отступ инпута */
    border: 1px solid #ccc; /* Цвет рамки инпута */
    border-radius: 4px; /* Радиус углов инпута */
    box-sizing: border-box; /* Учитываем границы и padding при расчете размера */
}

.inputWrapper {
    position: relative; /* Позиция относительно родителя */
}

.superInput {
    /* Основные стили для инпута, если нужны */
}

.errorInput {
    border-color: #ff0000; /* Цвет рамки при наличии ошибки */
}

.errorInput:focus {
    border-color: #ff0000; /* Цвет рамки при фокусе с ошибкой */
    box-shadow: 0 0 5px rgba(255, 0, 0, 0.5); /* Тень при фокусе с ошибкой */
}

.error {
    color: #ff0000; /* Цвет текста ошибки */
    font-size: 12px; /* Размер текста ошибки */
}
